/* common */

@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.9/dist/web/static/pretendard.min.css");

:root {
  --point_color: #997dff;
  --black: #222;
  --gray: #555;
  --white: #fff;
}

* {
  margin: 0;
  box-sizing: border-box;
  color: var(--black);
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1;
  text-align: center;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
  font-family: 'Pretendard', sans-serif;
}

h1 {
  position: absolute;
  top: 0;
  left: 0;
  padding: 20px;
  font-size: 25px;
}

h2,
.score .score_box {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 300px;
  height: 300px;
  padding: 50px;
  margin: 0 auto 30px;
  border: 1px solid var(--black);
  border-radius: 10px;
  word-break: keep-all;
  font-size: 1.25rem;
  line-height: 1.4;
}

.OXButton {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  margin: auto;
  width: 300px;
}

button {
  width: 100%;
  height: 7rem;
  border-radius: 10px;
  border: 1px solid var(--point_color);
  color: var(--point_color);
  font-size: 30px;
  font-weight: 600;
  background-color: var(--white);
  transition: all 0.2s;
  cursor: pointer;
}

button:hover {
  color: var(--white);
  background-color: var(--point_color);
}

/* answer */

.answer button,
.score button {
  font-size: 1.2rem;
  border: 1px solid var(--black);
  color: var(--black);
  font-size: 1rem;
}

.answer button:hover,
.score button:hover {
  color: var(--white);
  background-color: var(--black);
}

.answer h2,
.score .score_box {
  border: 1px solid var(--point_color);
  color: var(--point_color);
}

/* score */

.score {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.score .score_box {
  flex-direction: column;
  gap: 30px;
}

.score .score_box p {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 10px;
  font-size: 0.9rem;
}

.score .score_box p b{
  color: var(--point_color);
  font-size: 1.25rem;
}